"use client";

import { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import AdminLogin from "@/components/admin/AdminLogin";
import { authAPI, newsAPI, projectsAPI } from "@/lib/api";

interface Admin {
  id: string;
  email: string;
  name: string | null;
}

export default function AdminPage() {
  const [admin, setAdmin] = useState<Admin | null>(null);
  const [activeTab, setActiveTab] = useState<'dashboard' | 'news' | 'projects'>('dashboard');
  const [isLoading, setIsLoading] = useState(true);

  // Check if user is already logged in
  useEffect(() => {
    const checkAuth = async () => {
      try {
        const response = await authAPI.verify();
        setAdmin(response.admin);
      } catch (error) {
        console.log('No valid admin session');
      } finally {
        setIsLoading(false);
      }
    };

    checkAuth();
  }, []);

  const handleLoginSuccess = (adminData: Admin) => {
    setAdmin(adminData);
  };

  const handleLogout = async () => {
    try {
      await authAPI.logout();
      setAdmin(null);
      setActiveTab('dashboard');
    } catch (error) {
      console.error('Logout error:', error);
    }
  };

  // Show loading while checking authentication
  if (isLoading) {
    return (
      <div className="min-h-screen bg-omran-light flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-omran-teal mx-auto mb-4"></div>
          <p className="text-gray-600">ุฌุงุฑู ุงูุชุญูู ูู ุตุญุฉ ุชุณุฌูู ุงูุฏุฎูู...</p>
        </div>
      </div>
    );
  }

  // Show login if not authenticated
  if (!admin) {
    return <AdminLogin onLoginSuccess={handleLoginSuccess} />;
  }

  return (
    <div className="min-h-screen bg-omran-light">
      {/* Header */}
      <header className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-4 space-x-reverse">
              <h1 className="text-2xl font-bold text-omran-teal">ููุญุฉ ุชุญูู ุงูุฅุฏุงุฑุฉ</h1>
              <span className="text-gray-500">|</span>
              <span className="text-gray-600">ูุฑุญุจุงูุ {admin.name || admin.email}</span>
            </div>
            <Button
              onClick={handleLogout}
              variant="outline"
              className="border-red-500 text-red-500 hover:bg-red-500 hover:text-white"
            >
              ุชุณุฌูู ุงูุฎุฑูุฌ
            </Button>
          </div>
        </div>
      </header>

      <div className="max-w-7xl mx-auto px-4 py-8">
        {/* Tab Navigation */}
        <div className="bg-white rounded-lg shadow-sm mb-8">
          <div className="border-b border-gray-200">
            <nav className="flex space-x-8 space-x-reverse px-6">
              <button
                onClick={() => setActiveTab('dashboard')}
                className={`py-4 px-1 border-b-2 font-medium text-sm ${
                  activeTab === 'dashboard'
                    ? 'border-omran-teal text-omran-teal'
                    : 'border-transparent text-gray-500 hover:text-gray-700'
                }`}
              >
                ุงูุฑุฆูุณูุฉ
              </button>
              <button
                onClick={() => setActiveTab('news')}
                className={`py-4 px-1 border-b-2 font-medium text-sm ${
                  activeTab === 'news'
                    ? 'border-omran-teal text-omran-teal'
                    : 'border-transparent text-gray-500 hover:text-gray-700'
                }`}
              >
                ุฅุฏุงุฑุฉ ุงูุฃุฎุจุงุฑ
              </button>
              <button
                onClick={() => setActiveTab('projects')}
                className={`py-4 px-1 border-b-2 font-medium text-sm ${
                  activeTab === 'projects'
                    ? 'border-omran-teal text-omran-teal'
                    : 'border-transparent text-gray-500 hover:text-gray-700'
                }`}
              >
                ุฅุฏุงุฑุฉ ุงููุดุงุฑูุน
              </button>
            </nav>
          </div>
        </div>

        {/* Tab Content */}
        <div>
          {activeTab === 'dashboard' && <DashboardOverview />}
          {activeTab === 'news' && <EnhancedNewsManager />}
          {activeTab === 'projects' && <EnhancedProjectsManager />}
        </div>
      </div>
    </div>
  );
}

